.App {
  text-align: left;
}

.App-Scroll-Page {
  min-height: 100vh;
  background-color: #efefef;
}

.container.PhaserJS {
  text-align: left;
}

.datalist dt {
  margin-top: 20px;
}

.datalist dd {
  margin-top: 12px;
}

.heading {
  margin-top: 40px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 150px;
  /* flex-direction: column; */
  display: flex;
  /* align-items: center; */
  /* justify-content: center; */
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Profile {
  margin-left: auto;
  margin-right: auto;
}

h1 {
  font-size: 64px;
}

h2 {
  margin-top: 30px;
  margin-bottom: 30px;
}

.table.table-bordered {
  font-weight: bold;
}

table.table.table-bordered tbody {
  font-size: 13px;
}

table.table.table-bordered thead {
  font-size: 19px;
}

.flex-right {
  align-items: center;
  vertical-align: middle;
  padding: 10px 20px;
}

.flex-left {
  padding: 10px 20px;
}

.unstyled-list {
  list-style-type: none;
  padding-left: 0;
  text-align: right;
}

.unstyled-list li {
  margin-left: 10px;
  padding: 6px;
  display: inline-block;
  background-color: #888888;
  border-radius: 4px;
}

.unstyled-list li:hover {
  background-color: #9a9a9a;
}

.unstyled-list li a {
  color: white;
  font-size: 14px;
  padding: 0;
}

.portfolio-row {
  display: flex;
  opacity: 0.45;
}

.portfolio {
  flex: 1;
  margin-right: 5px;
  margin-bottom: 5px;
}

.main-portfolio h1, .main-portfolio h2, .main-portfolio h3 {
  color: white !important;
  background-color: gray !important;
  opacity: 0.6 !important;
  text-align: center;
}

.portfolio-description p a {
  padding: 8px 10px;
  background-color: gray !important;
  opacity: 0.75;
  color: white !important;
  border-radius: 4px;
}
